import React from 'react';

const List = props =>
{
return (
	<ul>
    {
			props.items.map((item, index) => {
				return (
				<li key={index}>
		<div className="list-item">
		<span>
		{item}
		</span>
				<button onClick={props.items.splice(index,1)}>X</button>
		</div>
				</li>
				
				)
			}
			
			)
		}
  </ul>
)};

export default List;